* {
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
}

body {
    margin: 0px;
    background-color: #ffffff;
    overflow: hidden;
}

header {
    display: table;
    position: absolute;
    left: 0;
    top: 0;
    right: 0;
    width: 100%;
    height: 2rem;
    line-height: 2rem;
    z-index: 10;
    color: #eee;
    background: rgba(0,0,0,.8);
    text-align: center;
}

header > div {
    display: table-cell;
    text-align: right;
    vertical-align: center;
    padding: 0 .5rem;
}

header > div:first-child {
    width: 6rem;
    text-align: left;
}

